import { Lightning, Utils } from 'wpe-lightning-sdk'

import screen from '../screen'

export default class AlbumArt extends Lightning.Component {
  static _template() {
    return {
      Container: {
        mountX: 0.5,
        x: screen.w / 2 + 15,
        y: 280,
        src: Utils.asset('images/png/headphones.png'),
        w:500,
        h:500,
       // texture: Lightning.Tools.getSvgTexture(Utils.asset('images/headphones.svg'), 500, 500)
      }
    }
  }
}
