import { Lightning, Utils } from 'wpe-lightning-sdk'

import screen from '../screen'
import theme from '../theme'

class ControlButton extends Lightning.Component {
  _focus() {
    this.patch({
      texture: Lightning.Tools.getRoundRect(60, 60, 30, 3, theme.primary, true, theme.primary)
    })
  }

  _unfocus() {
    this.patch({
      texture: Lightning.Tools.getRoundRect(60, 60, 30, 3, theme.devicesBG, true, theme.devicesBG)
    })
  }
}

export default class Controls extends Lightning.Component {
  static _template() {
    return {
      Container: {
        flex: { direction: 'row' },
        y: screen.h - 60,
        x: screen.w / 2,
        mountY: 1,
        mountX: 0.5,
        SkipBack: {
          type: ControlButton,
          texture: Lightning.Tools.getRoundRect(
            60,
            60,
            30,
            3,
            theme.devicesBG,
            true,
            theme.devicesBG
          ),
          SkipBackIcon: {
            x: 60 / 2 + 3,
            y: 60 / 2 + 2,
            mount: 0.5,
            src: Utils.asset('images/png/wireSkipBwd.png'),
            w:30,
            h:30,
           // texture: Lightning.Tools.getSvgTexture(Utils.asset('images/png/wireSkipBwd.svg'), 30, 30)
          }
        },
        PlayPause: {
          type: ControlButton,
          x: 15,
          texture: Lightning.Tools.getRoundRect(
            60,
            60,
            30,
            3,
            theme.devicesBG,
            true,
            theme.devicesBG
          ),
          PlayIcon: {
            alpha: 0,
            x: 60 / 2 + 5,
            y: 60 / 2 + 2,
            mount: 0.5,
            src: Utils.asset('images/png/wirePlay.png'),
            w:30,
            h:30,
            //texture: Lightning.Tools.getSvgTexture(Utils.asset('images/player/play.png'), 30, 30)
          // texture: Lightning.Tools.getSvgTexture(Utils.asset('images/png/wirePlay.svg'), 30, 30)
          },
          PauseIcon: {
            x: 60 / 2 + 1.5,
            y: 60 / 2 + 2,
            mount: 0.5,
            src: Utils.asset('images/png/wirePause.png'),
            w:30,
            h:30,
           // texture: Lightning.Tools.getSvgTexture(Utils.asset('images/player/pause.png'), 30, 30)
            //texture: Lightning.Tools.getSvgTexture(Utils.asset('images/png/wirePause.svg'), 30, 30)
          }
        },
        SkipForward: {
          type: ControlButton,
          x: 30,
          texture: Lightning.Tools.getRoundRect(
            60,
            60,
            30,
            3,
            theme.devicesBG,
            true,
            theme.devicesBG
          ),
          SkipForwardIcon: {
            x: 60 / 2 + 3,
            y: 60 / 2 + 2,
            mount: 0.5,
            src: Utils.asset('images/png/wireSkipFwd.png'),
            w:30,
            h:30,
            //texture: Lightning.Tools.getSvgTexture(Utils.asset('images/png/wireSkipFwd.svg'), 30, 30)
          }
        }
      }
    }
  }

  _init() {
    this.index = 1
    this.isPlaying = true
  }

  get child() {
    return this.tag('Container').children[this.index]
  }

  _getFocused() {
    return this.child
  }

  _handleLeft() {
    if (this.index > 0) {
      this.index--
    }
  }

  _handleRight() {
    if (this.index < this.tag('Container').children.length - 1) {
      this.index++
    }
  }

  updatePlayPause(action){
if(action==="paused"){
    this.tag('PlayPause.PauseIcon').patch({ alpha: 0 })
    this.tag('PlayPause.PlayIcon').patch({ alpha: 1})
  }else{
    this.tag('PlayPause.PauseIcon').patch({ alpha: 1 })
    this.tag('PlayPause.PlayIcon').patch({ alpha: 0 })
  }
  }
  _handleEnter() {
    switch (this.index) {
      case 1:
        this.isPlaying = !this.isPlaying

        if (this.isPlaying) {
          
          this.tag('PlayPause.PauseIcon').patch({ alpha: 1 })
          this.tag('PlayPause.PlayIcon').patch({ alpha: 0 })
          this.fireAncestors('$pause')
        } else {
          
          this.tag('PlayPause.PauseIcon').patch({ alpha: 0 })
          this.tag('PlayPause.PlayIcon').patch({ alpha: 1 })
          this.fireAncestors('$play')
        }
        break
      case 0:
        this.fireAncestors('$sendAudioPlayBackCommand',"SKIP_PREVIOUS")
        break
      case 2:
        this.fireAncestors('$sendAudioPlayBackCommand',"SKIP_NEXT")
        break
    }
  }
}
