import { Lightning } from 'wpe-lightning-sdk'

import screen from './screen'
import theme from './theme'

export default class Header extends Lightning.Component {
  static _template() {
    return {
      Container: {
        w: screen.w - 100,
        h: 80,
        y: 60,
        x: 50,
        flex: { direction: 'row', justifyContent: 'space-between', alignItems: 'center' },
        Title: {
          color: theme.primary,
          text: { text: 'XFinity Bluetooth Audio', fontSize: 30, fontFace: 'Default' }
        },
        Time: {
          color: theme.primary,
          text: { text: '', fontSize: 30, fontFace: 'Default' }
        }
      }
    }
  }

  _active() {
    this.tag('Title').patch({
      text: { text: this.title ? this.title : 'XFinity Bluetooth Audio' }
    })
  }

  updateTime() {
    let date = new Date()
    let hours = date.getHours()
    let minutes = date.getMinutes()
    let ampm = hours >= 12 ? 'PM' : 'AM'
    hours = hours % 12
    hours = hours ? hours : 12
    minutes = minutes < 10 ? '0' + minutes : minutes

    let strTime = hours + ':' + minutes + ' ' + ampm

    this.tag('Time').patch({ text: { text: strTime } })
  }

  _init() {
    setInterval(() => {
      this.updateTime()
    }, 1000)
  }
}
